#error404 {
    background-color: #fff;
    height: 94vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

#error404 svg {
    position: absolute;
    top: 50%;
    left: 50%;
    margin-top: -250px;
    margin-left: -400px;
}

#error404 .message-box {
    height: 200px;
    width: 100%;
    /* position: absolute;
    top: 50%;
    left: 50%; */
    margin: auto;
    /* margin-top: -100px; */
    color: #006494;
    font-family: Roboto;
    font-weight: 300;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}

#error404 .message-text-div {
    display: flex;
    justify-content: center;
    align-items: center;
}

#error404 .message-box h1 {
    font-size: 60px;
    line-height: 46px;
    margin-bottom: 40px;
    padding: 15px;
    border-left: 1px solid #c0c0c0;
    color: #c0c0c0;
}

#error404 .buttons-con .action-link-wrap {
    margin-top: 40px;
}

#error404 .buttons-con .action-link-wrap a {
    background: #006494;
    padding: 8px 25px;
    border-radius: 4px;
    color: #FFF;
    font-weight: bold;
    font-size: 14px;
    -webkit-transition: all 0.3s linear;
    transition: all 0.3s linear;
    cursor: pointer;
    text-decoration: none;
    margin-right: 10px
}

#error404 .buttons-con .action-link-wrap a:hover {
    background: #5A5C6C;
    color: #fff;
}

#Polygon-1,
#Polygon-2,
#Polygon-3,
#Polygon-4,
#Polygon-4,
#Polygon-5 {
    -webkit-animation: float 1s infinite ease-in-out alternate;
            animation: float 1s infinite ease-in-out alternate;
}

#Polygon-2 {
    -webkit-animation-delay: .2s;
            animation-delay: .2s;
}

#Polygon-3 {
    -webkit-animation-delay: .4s;
            animation-delay: .4s;
}

#Polygon-4 {
    -webkit-animation-delay: .6s;
            animation-delay: .6s;
}

#Polygon-5 {
    -webkit-animation-delay: .8s;
            animation-delay: .8s;
}

@-webkit-keyframes float {
    100% {
        -webkit-transform: translateY(20px);
                transform: translateY(20px);
    }
}

@keyframes float {
    100% {
        -webkit-transform: translateY(20px);
                transform: translateY(20px);
    }
}

@media (max-width: 450px) {
    #error404 svg {
        position: absolute;
        top: 50%;
        left: 50%;
        margin-top: -250px;
        margin-left: -190px;
    }
    #error404 .message-box {
        top: 50%;
        left: 50%;
        margin-top: -100px;
        margin-left: -190px;
        text-align: center;
    }
}
